import { useEffect } from "react";
import useTelegramInitData from "../../../hooks/useTelegramInitData";
import { useLogsQuery } from "../../../queries/mutations";
import useStore from "../../../store";
import { useNavigate } from "react-router-dom";
import InfiniteScroll from 'react-infinite-scroller'
import { useInfiniteQuery, useQuery } from "react-query";
import { getCollections, getDevelopments } from "../../../api";
import { ColorRing } from 'react-loader-spinner'
import Item from "./components/Item";
import Filters from "./components/Filters";

const Collection = () => {
  const { tg } = useTelegramInitData();
  const logsMutation = useLogsQuery();
  const id = useStore((state) => state.data.id);
  const activeDevelopment = useStore((state) => state.data.activeDevelopment);
  const setActiveDevelopment = useStore((state) => state.setActiveDevelopment);
  const cities = useStore((state) => state.data.user.cities);
  const clearQuestions = useStore((state) => state.clearQuestions);
  const tags = useStore((state) => state.data.tags);
  const navigate = useNavigate()
  const fetchCollection = ({ pageParam = 1, cities, activeDevelopment }) => {
    logsMutation.mutate({
      telegram_user: id,
      Action: `   ${pageParam}`,
    });
    return getCollections({
      page: pageParam,
      pageSize: 10,
      cities: cities,
      activeDevelopment: activeDevelopment,
      tags: tags
    });
  };

  const handleDevelopment = (item) => {
    setActiveDevelopment(item, logsMutation)
  }

  const {
    data: dataInfinity,
    fetchNextPage,
    hasNextPage,
    isLoading,
    isFetching,
  } = useInfiniteQuery({
    queryKey: ['Collection', cities, activeDevelopment, tags],
    queryFn: (page) => fetchCollection({ pageParam: page.pageParam, cities, activeDevelopment, tags }),
    getNextPageParam: (last, allPages) => {
      return allPages.length <
        last?.data?.meta?.pagination?.pageCount
        ? allPages.length + 1
        : undefined;
    },
  });

  const { isLoading: isLoadingDevelopments, data: dataDevelopments } = useQuery(
    [
      'developments',
      cities,
      tags
    ],
    () =>
      getDevelopments({ cities: cities, tags: tags }),
  );

  useEffect(() => {
    tg.BackButton.hide();
  }, []);

  const handleRepeat = () => {
    logsMutation.mutate({
      telegram_user: id,
      Action: "        ",
    });
    tg.BackButton.hide();
    clearQuestions()
    navigate("/main");
  }

  const handleSubscriptions = () => {
    logsMutation.mutateAsync({
      telegram_user: id,
      Action: "         ",
    });
    tg.BackButton.hide();
    navigate("/subscriptions");
  }
  const handleFinish = () => {
    logsMutation.mutateAsync({
      telegram_user: id,
      Action: "        ",
    });
    tg.BackButton.hide();
    tg.close()
  }

  return (
    <div className="flex-col w-full">
      <div className="p-7 pb-2">
        <p className="text-center mb-2">
             <br />  
        </p>
        <div className="mb-7 flex justify-center">
          <div className="bg-contain h-5 w-20 bg-center bg-[url('./assets/images/arrow-bottom-red.svg')] bg-no-repeat" />
        </div>
      </div>
      {!isLoadingDevelopments ? <Filters activeDevelopment={activeDevelopment} setActiveDevelopment={handleDevelopment} data={dataDevelopments?.data?.data} /> : <div className="flex pb-5 flex-grow h-full items-center justify-center"><ColorRing
        visible={true}
        height="30"
        width="30"
        ariaLabel="blocks-loading"
        wrapperStyle={{}}
        wrapperClass="blocks-wrapper"
        colors={['#92278f', '#c6168d', '#ed1c24', '#c6168d', '#92278f']}
      /></div>}
      {!isLoading ? <InfiniteScroll
        pageStart={0}
        initialLoad={false}
        loadMore={fetchNextPage}
        hasMore={hasNextPage && !isFetching}
        loader={<div className="flex justify-center"><ColorRing
          visible={true}
          height="80"
          width="80"
          ariaLabel="blocks-loading"
          wrapperStyle={{}}
          wrapperClass="blocks-wrapper"
          colors={['#92278f', '#c6168d', '#ed1c24', '#c6168d', '#92278f']}
        /></div>}
      >
        <div className="flex flex-wrap">
          {dataInfinity?.pages.map(page =>
            page.data.data.map((item) => (
              <>
                <Item navigate={navigate} key={item.id} item={item} />
              </>
            ))
          )}
          {!hasNextPage && <div className={`p-5 ${dataInfinity?.pages[0]?.data?.meta?.pagination?.total % 2 === 0 ? "" : 'w-2/4'} bg-gradient-to-r from-[#92278f] via-[#c6168d] to-[#ed1c24]`}><p className="text-xs text-white opacity-50 mb-3"> 
              
             ?</p>
            <div className="cursor-pointer py-4 flex justify-center items-center text-xs mb-1 bg-[rgba(255,255,255,0.2)] text-white rounded active:bg-[rgba(255,255,255,0.4)] transition-all duration-500 active:scale-95 active:brightness-105" onClick={() => handleRepeat()}> </div>
            <div className="cursor-pointer py-4 flex justify-center items-center text-xs mb-1 bg-[rgba(255,255,255,0.2)] text-white rounded active:bg-[rgba(255,255,255,0.4)] transition-all duration-500 active:scale-95 active:brightness-105" onClick={() => handleSubscriptions()}> </div>
            <div className="cursor-pointer py-4 flex justify-center items-center text-xs bg-[rgba(255,255,255,0.2)] text-white rounded active:bg-[rgba(255,255,255,0.4)] transition-all duration-500 active:scale-95 active:brightness-105" onClick={() => handleFinish()}> </div></div>}
        </div>
      </InfiniteScroll> : <div className="flex flex-grow h-full items-center justify-center"><ColorRing
        visible={true}
        height="80"
        width="80"
        ariaLabel="blocks-loading"
        wrapperStyle={{}}
        wrapperClass="blocks-wrapper"
        colors={['#92278f', '#c6168d', '#ed1c24', '#c6168d', '#92278f']}
      /></div>}
    </div>
  );
};

export default Collection;
