import { create } from "zustand";

const useStore = create((set, get) => ({
  data: {
    nameSuccessBack: false,
    id: "",
    questions: [],
    tags: [],
    activeDevelopment: [],
    user: {
      UserTelegamID: "",
      UserTelegramUsername: "",
      UserTelegramName: "",
      UserName: "",
      subscriptions: [],
      cities: [],
    },
  },
  setNameSuccessBack: (nameSuccessBack) =>
    set((state) => ({
      data: { ...state.data, nameSuccessBack: nameSuccessBack },
    })),
  setActiveDevelopment: (item, mutation) => {
    const activeDevelopment = get().data.activeDevelopment;
    const id = get().data.id;
    if (activeDevelopment?.find(el => el.id === item.id)) {
      const activeDevelopmentDelete = activeDevelopment.filter(el => el.id !== item.id);
      mutation.mutate({
        telegram_user: id,
        Action: `   `,
      });
      set((state) => ({
        data: { ...state.data, activeDevelopment: [...activeDevelopmentDelete] },
      }))
    } else {
      mutation.mutate({
        telegram_user: id,
        Action: `    ${item?.attributes?.district}`,
      });
      set((state) => ({
        data: { ...state.data, activeDevelopment: [...activeDevelopment, item] },
      }))
    }
  },
  clearActiveDevelopment: () => set((state) => ({
    data: { ...state.data, activeDevelopment: [] },
  })),
  setUser: (user) =>
    set((state) => ({
      data: { ...state.data, user: { ...user } },
    })),
  setQuestions: (answer) => {
    const questions = get().data.questions;
    if (questions.find(item => item.id === answer.id)) {
      const questionsDelete = questions.filter(item => item.id !== answer.id);
      set((state) => ({
        data: { ...state.data, questions: [...questionsDelete, answer] },
      }))
    } else {
      set((state) => ({
        data: { ...state.data, questions: [...questions, answer] },
      }))
    }
  },
  clearQuestions: () =>
    set((state) => ({
      data: { ...state.data, questions: [], tags: [] },
    })),
  setTags: () => {
    const questions = get().data.questions;
    const newTags = [];
    questions.forEach(item => {
      item?.answer?.forEach(el => {
        if (el?.app_tags?.data?.length) {
          el?.app_tags?.data.forEach(tag => newTags.push(tag?.id))
        }
      })
    })
    set((state) => ({
      data: { ...state.data, tags: Array.from(new Set(newTags)) }
    }))
  },
  setUserName: (name) =>
    set((state) => ({
      data: { ...state.data, user: { ...state.data.user, UserName: name } },
    })),
  setId: (id) =>
    set((state) => ({
      data: { ...state.data, id },
    })),
  updateUserStore: (mutation) => {
    const id = get().data.id;
    const user = get().data.user;
    mutation.mutate({ data: { ...user }, id });
  },
  setCities: (city, mutation) => {
    const cities = get().data.user.cities;
    const id = get().data.id;
    if (cities?.find((item) => item.id === city.id)) {
      mutation.mutate({
        telegram_user: id,
        Action: `  ${city?.attributes?.CityName}`,
      });
      set((state) => ({
        data: {
          ...state.data,
          user: {
            ...state.data.user,
            cities: cities.filter((item) => item.id !== city.id),
          },
        },
      }));
    } else {
      mutation.mutate({
        telegram_user: id,
        Action: `  ${city?.attributes?.CityName}`,
      });
      set((state) => ({
        data: {
          ...state.data,
          user: {
            ...state.data.user,
            cities: [...cities, city],
          },
        },
      }));
    }
  },
  setSubscriptions: (subscription, mutation) => {
    const subscriptions = get().data.user.subscriptions;
    const id = get().data.id;
    if (subscriptions?.find((item) => item.id === subscription.id)) {
      mutation.mutate({
        telegram_user: id,
        Action: `  ${subscription?.attributes?.SubscriptionTitle}`,
      });
      set((state) => ({
        data: {
          ...state.data,
          user: {
            ...state.data.user,
            subscriptions: subscriptions.filter(
              (item) => item.id !== subscription.id
            ),
          },
        },
      }));
    } else {
      mutation.mutate({
        telegram_user: id,
        Action: `  ${subscription?.attributes?.SubscriptionTitle}`,
      });
      set((state) => ({
        data: {
          ...state.data,
          user: {
            ...state.data.user,
            subscriptions: [...subscriptions, subscription],
          },
        },
      }));
    }
  },
}));

export default useStore;
