import { useQueryClient } from 'react-query'
import { CollectionButton } from './CollectionButton'
import { useNavigate } from 'react-router-dom'
import { useLogsQuery } from '../../../queries/mutations'
import { Modal } from '../../ui/Modal'
import { useState } from 'react'
import { CallMeForm } from '../Forms/CallMeForm'

export const CollectionItemActions = ({
  currentIndexPage,
  id,
  cities,
  activeDevelopment,
  tags,
}) => {
  const [open, setOpen] = useState(false)
  const queryClient = useQueryClient()
  const navigate = useNavigate()
  const logsMutation = useLogsQuery()

  const handleDialog = () => {
    logsMutation.mutate({
      telegram_user: id,
      Action: `      `,
    })
    navigate('/thanksPage')
  }

  const handleTel = () => {
    logsMutation.mutate({
      telegram_user: id,
      Action: `    `,
    })
    navigate('/thanksPage')
  }
  const handleBackButton = () => {
    queryClient.setQueryData(
      ['Collection', cities, activeDevelopment, tags],
      (data) => {
        const newPage = data.pages
        const newParams = data.pageParams
        newPage.splice(currentIndexPage === 0 ? 1 : currentIndexPage, 1000000)
        newParams.splice(currentIndexPage === 0 ? 1 : currentIndexPage, 1000000)
        return {
          pages: newPage,
          pageParams: newParams,
        }
      }
    )
    logsMutation.mutate({
      telegram_user: id,
      Action: `          `,
    })
    navigate(-1)
  }
  const handleTelMe = () => {
    setOpen(true)
    logsMutation.mutate({
      telegram_user: id,
      Action: `    `,
    })
  }

  return (
    <>
      <div className="mt-7 flex flex-col gap-2 mb-5">
        <CollectionButton
          color="red"
          title=" "
          onClick={() => handleTelMe()}
        />
        <CollectionButton
          color="violet"
          title="   "
          onClick={() => handleDialog()}
        />
        <CollectionButton
          color="violet"
          title=" "
          onClick={() => handleTel()}
        />
        <Modal open={open} setOpen={setOpen}>
         <CallMeForm id={id} setOpen={setOpen} />
        </Modal>
      </div>
      <CollectionButton
        color="gray"
        title="  "
        onClick={() => handleBackButton()}
      />
    </>
  )
}
