import * as Yup from 'yup'
const phoneRegExp = /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{2}\)?)\s?-?\s?(\(?\d{2}\)?)?$/


export const callMeFormSchema = Yup.object().shape({
    tel: Yup.string().matches(phoneRegExp, 'Phone number is not valid')
      .required(' '),
  })

  export const callMeInitialValues = {tel: ''}