import axios from "axios";
import { baseApi } from "../variables";

export const getCities = async () => {
  return await axios.get(`${baseApi}/cities`);
};

export const getSubscriptions = async () => {
  return await axios.get(`${baseApi}/subscriptions`);
};

export const getQuestions = async () => {
  return await axios.get(`${baseApi}/questions/?populate[QuestionAnswers][populate]=*`);
};

export const getUser = async (id) => {
  return await axios.get(
    `${baseApi}/telegram-users/?populate[subscriptions][populate]&populate[cities][populate]&filters[UserTelegamID][$eq]=${id}`
  );
};

export const registerUser = async (data) => {
  return await axios.post(`${baseApi}/telegram-users?populate[subscriptions][populate]&populate[cities][populate]`, {
    data,
  });
};

export const setLogs = async (data) => {
  return await axios.post(`${baseApi}/user-logs`, {
    data,
  });
};

export const updateUser = async ({ data, id }) => {
  return await axios.put(`${baseApi}/telegram-users/${id}`, {
    data,
  });
};

export const getCollections = async ({ page, pageSize, cities, activeDevelopment, tags }) => {
  const citiesNames = cities?.map(item => item?.attributes?.CityName);
  let citiesNamesData = '';
  if (citiesNames?.length) {
    for (let i = 0; i < citiesNames?.length; i++) {
      citiesNamesData += `&filters[city][CityName][${i}]=${citiesNames[i]}`;
    }
  }
  const tagsIds = tags?.map(item => item);
  let tagsData = '';
  if (tagsIds?.length) {
    for (let i = 0; i < tagsIds?.length; i++) {
      tagsData += `&filters[tags][id][$in][${i}]=${tagsIds[i]}`;
    }
  }
  const activeDevelopmentArray = activeDevelopment?.map(item => item?.attributes?.ProfitbaseId);
  let activeDevelopmentData = '';
  if (activeDevelopmentArray?.length) {
    for (let i = 0; i < activeDevelopmentArray?.length; i++) {
      activeDevelopmentData += `&filters[development][ProfitbaseId][${i}]=${activeDevelopmentArray[i]}`;
    }
  }
  return await axios.get(
    `${baseApi}/apartments?populate=*${cities?.length ? citiesNamesData : ''}${activeDevelopmentArray?.length ? activeDevelopmentData : ''}${tagsIds?.length ? tagsData : ''}&pagination[pageSize]=${pageSize}&pagination[page]=${page}&sort[0]=createdAt%3Adesc&filters[price][value][$gt]=1`
  );
};

export const getDevelopments = async ({ cities, tags }) => {
  const citiesNames = cities?.map(item => item?.attributes?.CityName);
  let citiesNamesData = '';
  if (citiesNames?.length) {
    for (let i = 0; i < citiesNames?.length; i++) {
      citiesNamesData += `&filters[city][CityName][${i}]=${citiesNames[i]}`;
    }
  }
  const tagsIds = tags?.map(item => item);
  let tagsData = '';
  if (tagsIds?.length) {
    for (let i = 0; i < tagsIds?.length; i++) {
      tagsData += `&filters[apartments][tags][id][$in][${i}]=${tagsIds[i]}`;
    }
  }
  return await axios.get(
    `${baseApi}/developments?${cities?.length ? citiesNamesData : ''}${tagsIds?.length ? tagsData : ''}&pagination[pageSize]=10000&pagination[page]=1&sort[0]=createdAt%3Adesc&filters[apartments][price][value][$gt]=1`
  );
};

export const pushPhoneNumber = async (data) => {
  return await axios.post(`${baseApi}/submissions`, { data })

}