import { useEffect, useState, useRef } from 'react'
import { useNavigate, useLocation } from 'react-router-dom'
import useTelegramInitData from '../../../hooks/useTelegramInitData'
import { useLogsQuery } from '../../../queries/mutations'
import useStore from '../../../store'
import { useInfiniteQuery, useQueryClient } from 'react-query'
import { getCollections } from '../../../api'
import { ImgSlider } from '../../blocks/CollectionItems/ImgSlider'
import { CollectionItemActions } from '../../blocks/CollectionItems/CollectionItemActions'
import { Map, Placemark } from '@pbe/react-yandex-maps';
import dayjs from 'dayjs';
import quarterOfYear from 'dayjs/plugin/quarterOfYear'
// import { PDFDownloadLink, Document, Page, Text, View } from '@react-pdf/renderer';
dayjs.extend(quarterOfYear)


const CollectionItem = () => {
  const { tg } = useTelegramInitData()
  const clearQuestions = useStore((state) => state.clearQuestions);
  const logsMutation = useLogsQuery()
  const [item, setItem] = useState()
  const activeDevelopment = useStore((state) => state.data.activeDevelopment)
  const tags = useStore((state) => state.data.tags)
  const questions = useStore((state) => state.data.questions)
  const [currentIndexElement, setCurrentIndexElement] = useState(0)
  const [currentIndexPage, setCurrentIndexPage] = useState(0)
  const id = useStore((state) => state.data.id)
  const queryClient = useQueryClient()
  const navigate = useNavigate()
  const ref = useRef()
  const { state } = useLocation()
  const { idItem } = state
  const cities = useStore((state) => state.data.user.cities)
  const startDateValueOf = item?.attributes?.development?.data?.attributes?.startDate && dayjs(item?.attributes?.development?.data?.attributes?.startDate).endOf('date').valueOf() || ""
  const currentDateValueOf = dayjs().endOf('date').valueOf()
  const endDateValueOf = item?.attributes?.house?.builtYear && item?.attributes?.house?.readyQuarter && dayjs(item?.attributes?.house?.builtYear).quarter(item?.attributes?.house?.readyQuarter).endOf("month").valueOf() || ""
  const procent = item?.id ? Math.round((currentDateValueOf - startDateValueOf) / ((endDateValueOf - startDateValueOf) / 100)) > 99 ? 100 : Math.round((currentDateValueOf - startDateValueOf) / ((endDateValueOf - startDateValueOf) / 100)) : null

  const backHandle = () => {
    queryClient.setQueryData(
      ['Collection', cities, activeDevelopment, tags],
      (data) => {
        const newPage = data.pages
        const newParams = data.pageParams
        newPage.splice(currentIndexPage === 0 ? 1 : currentIndexPage, 1000000)
        newParams.splice(currentIndexPage === 0 ? 1 : currentIndexPage, 1000000)
        return {
          pages: newPage,
          pageParams: newParams,
        }
      }
    )
    logsMutation.mutateAsync({
      telegram_user: id,
      Action: `       `,
    })
    navigate(-1)
  }

  useEffect(() => {
    tg.onEvent('backButtonClicked', backHandle)
    return () => {
      tg.offEvent('backButtonClicked', backHandle)
    }
    // eslint-disable-next-line
  }, [backHandle])

  useEffect(() => {
    window.scrollTo(0, 0)
  }, [])

  useEffect(() => {
    tg.BackButton.show()
  }, [])

  const fetchCollection = ({
    pageParam = 1,
    cities,
    activeDevelopment,
    tags,
  }) => {
    logsMutation.mutate({
      telegram_user: id,
      Action: `   ${pageParam}`,
    })
    return getCollections({
      page: pageParam,
      pageSize: 10,
      cities: cities,
      activeDevelopment: activeDevelopment,
      tags: tags,
    })
  }

  const { fetchNextPage, hasNextPage, isFetching } = useInfiniteQuery({
    queryKey: ['Collection', cities, activeDevelopment, tags],
    queryFn: (page) =>
      fetchCollection({
        pageParam: page.pageParam,
        cities,
        activeDevelopment,
        tags,
      }),
    getNextPageParam: (last, allPages) => {
      return allPages.length < last?.data?.meta?.pagination?.pageCount
        ? allPages.length + 1
        : undefined
    },
    enabled: false,
  })

  const data = queryClient.getQueryData([
    'Collection',
    cities,
    activeDevelopment,
    tags,
  ])

  useEffect(() => {
    data?.pages?.map((item, i) => {
      item?.data?.data?.map((el, idx) => {
        if (el.id === idItem) {
          setItem(el)
          setCurrentIndexPage(i)
          setCurrentIndexElement(idx)
        }
      })
    })
  }, [])

  const onRepeat = () => {
    logsMutation.mutate({
      telegram_user: id,
      Action: "        ",
    });
    tg.BackButton.hide();
    clearQuestions()
    navigate("/main");
  }

  useEffect(() => {
    tg.onEvent("mainButtonClicked", onRepeat);
    return () => {
      tg.offEvent("mainButtonClicked", onRepeat);
    };
    // eslint-disable-next-line
  }, [onRepeat]);

  useEffect(() => {
    return () => {
      tg.MainButton.hide();
    }
  }, []);

  const handleScroll = () => {
    if (window.scrollY > 20 && window.scrollY - (document.documentElement.scrollHeight - document.documentElement.offsetHeight) > -20) {
      tg.MainButton.show();
      tg.MainButton.setParams({
        text: " ",
        color: "rgb(146, 39, 143)"
      });
    }
    if (window.scrollY > 20 && window.scrollY - (document.documentElement.scrollHeight - document.documentElement.offsetHeight) < -140) {
      tg.MainButton.hide()
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const nextItem = () => {
    const dataLength = data?.pages?.length
    const currentItemId = item.id
    let currentIndexPage = 0
    let currentIndexElement = 0
    let newItem = {}
    data?.pages?.forEach((item, i) => {
      item?.data?.data?.forEach((el, idx) => {
        if (el.id === currentItemId) {
          currentIndexPage = i
          currentIndexElement = idx
        }
      })
    })
    if (currentIndexElement !== 9) {
      newItem = data?.pages[currentIndexPage].data.data[currentIndexElement + 1]
      setCurrentIndexPage(currentIndexPage)
      setCurrentIndexElement(currentIndexElement + 1)
    } else {
      newItem = data?.pages[currentIndexPage + 1].data.data[0]
      setCurrentIndexPage(currentIndexPage + 1)
      setCurrentIndexElement(0)
    }
    setItem(newItem)
    newItem?.attributes?.image?.length &&
      logsMutation.mutateAsync({
        telegram_user: id,
        Action: `        id:${item.id}`,
      })
    if (
      currentIndexPage + 1 === dataLength &&
      (currentIndexElement + 1 === 9 || currentIndexElement + 1 === 10) &&
      hasNextPage
    ) {
      fetchNextPage()
    }
  }

  const checkNext = () => {
    let newItem = null
    if (currentIndexElement === 9 && !hasNextPage) {
      return false
    }
    if (currentIndexElement !== 9) {
      newItem =
        data?.pages[currentIndexPage]?.data?.data[currentIndexElement + 1]
    } else {
      newItem = data?.pages[currentIndexPage + 1]?.data?.data[0]
    }
    return !!newItem
  }

  const prevItem = () => {
    const currentItemId = item.id
    const dataLength = data?.pages?.length
    let currentIndexPage = 0
    let currentIndexElement = 0
    let newItem = {}
    data?.pages?.forEach((item, i) => {
      item?.data?.data?.forEach((el, idx) => {
        if (el.id === currentItemId) {
          currentIndexPage = i
          currentIndexElement = idx
        }
      })
    })
    if (currentIndexElement !== 0) {
      newItem = data?.pages[currentIndexPage].data.data[currentIndexElement - 1]
      setCurrentIndexPage(currentIndexPage)
      setCurrentIndexElement(currentIndexElement - 1)
    } else {
      newItem = data?.pages[currentIndexPage - 1].data.data[9]
      setCurrentIndexPage(currentIndexPage - 1)
      setCurrentIndexElement(9)
    }
    setItem(newItem)
    newItem?.attributes?.image?.length &&
      logsMutation.mutateAsync({
        telegram_user: id,
        Action: `        id:${item.id}`,
      })
    if (currentIndexPage - 1 !== -1 && currentIndexElement === 0) {
      queryClient.setQueryData(
        ['Collection', cities, activeDevelopment, tags],
        (data) => {
          const newPage = data.pages
          const newParams = data.pageParams
          newPage.splice(-1, dataLength)
          newParams.splice(-1, dataLength)
          return {
            pages: newPage,
            pageParams: newParams,
          }
        }
      )
    }
  }
  const textIpoteka = () => {
    if (questions.find(item => item?.id === 3)?.answer[0].QuestionAnswer !== "") {
      return '   '
    }
    if (questions.find(item => item?.id === 4)?.answer[0]?.QuestionAnswer === "  ") {
      return '   '
    }
    if (questions.find(item => item?.id === 4)?.answer[0]?.QuestionAnswer === " IT") {
      return '     IT - '
    }
    return ""
  }
  return (
    <div ref={ref} className="flex-col w-full overflow-x-hidden">
      <div className="px-7 pt-7">
        <div className="flex justify-between max-w-lg m-auto">
          <div
            onClick={() =>
              currentIndexPage === 0 && currentIndexElement === 0
                ? () => { }
                : prevItem()
            }
            className={`cursor-pointer flex [-webkit-tap-highlight-color:rgba(0,0,0,0)] transition-all duration-500 active:scale-95 active:brightness-105 active:border-none justify-center items-center h-[45px] w-[45px] rounded-full bg-defaultButton ${currentIndexPage === 0 && currentIndexElement === 0
              ? 'opacity-0 cursor-auto'
              : ''
              }`}
          >
            <div className="w-1/3 h-2/4 bg-contain bg-[url('./assets/images/prev.svg')] bg-no-repeat" />
          </div>
          <div className="w-3/5">
            {item?.attributes?.area?.value ? <p className="text-center text-xl">
              {item?.attributes?.property_type}{' '}
              {
                item?.attributes?.area?.value
              }
              
            </p> : item?.attributes?.property_type}
            <p className="text-center text-xs text-defaultButtonText">
              {item?.attributes?.city?.data?.attributes?.CityName} /{' '}
              {item?.attributes?.development?.data?.attributes?.name}
            </p>
          </div>
          <div
            onClick={() =>
              !checkNext() && !isFetching ? () => { } : !isFetching && nextItem()
            }
            className={`flex cursor-pointer [-webkit-tap-highlight-color:rgba(0,0,0,0)] transition-all duration-500 active:scale-95 active:brightness-105 active:border-none justify-center items-center h-[45px] w-[45px] rounded-full bg-defaultButton ${!checkNext() && !isFetching ? 'opacity-0 cursor-auto' : ''
              }`}
          >
            <div className="w-1/3 h-2/4 bg-contain bg-[url('./assets/images/next.svg')] bg-no-repeat" />
          </div>
        </div>
      </div>
      {item?.attributes?.image?.length ? <ImgSlider item={item} /> : null}
      <div className="px-7 pb-7">
        <div className='mt-2 p-1 bg-zinc-100 flex justify-center items-center text-xs text-itemDevelopment w-fit rounded leading-none'>  {item?.attributes?.price?.value.toLocaleString('ru') || 0} </div>
        {textIpoteka() ? <div className='mb-7 mt-2 rounded-lg bg-gradient-to-r p-[2px] from-[#92278f] via-[#c6168d] to-[#ed1c24]'><div className='bg-white rounded-lg p-4'><p className='text-sm leading-none'>{textIpoteka()}</p></div></div> : null}
        <div className="mt-7 mb-2 flex justify-between items-start">
          <div className="w-2/4 mr-1 text-left">
            <p className="text-defaultButtonText text-xs"> </p>
            <p className="text-textRed text-xs">
              {item?.attributes?.development?.data?.attributes?.name || ''}
            </p>
          </div>
          <div className="w-2/4 text-right">
            <p className="text-defaultButtonText text-xs"></p>
            <p className="text-xs">
              {item?.attributes?.floor &&
                item?.attributes?.floor + '  ' + item?.attributes?.house?.floorsTotal ||
                ''}
            </p>
          </div>
        </div>
        <div className="flex mb-2 justify-between items-start">
          <div className="w-2/4 mr-1 text-left">
            <p className="text-defaultButtonText text-xs"></p>
            <p className="text-xs">{item?.attributes?.house?.name || ''}</p>
          </div>
          <div className="w-2/4 text-right">
            <p className="text-defaultButtonText text-xs"> </p>
            <p className="text-xs">{item?.attributes?.number || 0}</p>
          </div>
        </div>
        <div className="flex justify-between items-start">
          <div className="w-2/4 mr-1 text-left">
            <p className="text-defaultButtonText text-xs">  </p>
            <p className="text-xs">
              {item?.attributes?.house?.builtYear && item?.attributes?.house?.readyQuarter && dayjs(item?.attributes?.house?.builtYear).quarter(item?.attributes?.house?.readyQuarter).endOf("month").format("DD.MM.YYYY") || ''}
            </p>
          </div>
          {procent ? <div className="w-2/4 mr-1 text-right flex flex-col items-end mt-1">
            <div className='w-[100px] h-1.5 bg-slate-200 rounded-full relative mb-1.5'>
              <div style={{ width: procent + 'px' }} className={`h-1.5 rounded-full absolute left-0 bg-gradient-to-r p-[2px] from-[#92278f] via-[#c6168d] to-[#ed1c24]`} />
            </div>
            <p className="text-xs">
               {procent}%
            </p>
          </div> : null}
        </div>
        {/* <p className='mt-2 text-defaultButtonText text-xs'>{item?.attributes?.description}</p> */}
        <div className="mt-5 mb-7 flex flex-wrap gap-1">
          {item?.attributes?.customField?.map((el) =>
            el?.value === "" ? (
              <div
                key={el.name}
                className="pb-1.5 pt-2 px-3 border-buttonSubmit border rounded"
              >
                <p className=" text-buttonSubmit font-medium uppercase text-xs">{el.name}</p>
              </div>
            ) : null
          )}
        </div>
        {item?.attributes?.development?.data?.attributes?.latitude && item?.attributes?.development?.data?.attributes?.longitude ? <div className='rounded-2xl overflow-hidden'>
          <Map width="100%" defaultState={{ center: [item?.attributes?.development?.data?.attributes?.latitude, item?.attributes?.development?.data?.attributes?.longitude], zoom: 13 }}>
            <Placemark defaultGeometry={[item?.attributes?.development?.data?.attributes?.latitude, item?.attributes?.development?.data?.attributes?.longitude]} />
          </Map>
        </div> : null}
        {/* <PDFDownloadLink document={<MyDoc />} fileName="somename.pdf">
          {({ loading }) =>
            loading ? 'Loading document...' : 'Download now!'
          }
        </PDFDownloadLink> */}
        {item?.id ? <CollectionItemActions
          currentIndexPage={currentIndexPage}
          id={id}
          cities={cities}
          activeDevelopment={activeDevelopment}
          tags={tags}
        /> : null}
      </div>
    </div>
  )
}

