import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import useStore from "../../../store/index";
import useTelegramInitData from "../../../hooks/useTelegramInitData";
import { useLogsQuery } from "../../../queries/mutations";
import { getQuestions } from "../../../api";
import { useQuery } from "react-query";
import SelectionMulti from "./components/SelectionMulti";
import Selection from "./components/Selection";
import InputAnswer from "./components/InputAnswer";
import Range from "./components/Range";

const Questions = () => {
  const { tg } = useTelegramInitData();
  const navigate = useNavigate();
  const logsMutation = useLogsQuery();
  const [activeQuestion, setActiveQuestion] = useState(0)
  const [showMainButton, setShowMainButton] = useState(false)
  const id = useStore((state) => state.data.id);
  const questions = useStore((state) => state.data.questions);
  const { setQuestions, setTags } = useStore((state) => state);
  const { data } = useQuery(["questions"], () => getQuestions());

  const backHandle = () => {
    if (activeQuestion !== 0) {
      setActiveQuestion(activeQuestion - 1)
      logsMutation.mutateAsync({
        telegram_user: id,
        Action: `         ${data?.data?.data[activeQuestion].id}`,
      });
    } else {
      tg.BackButton.hide();
      logsMutation.mutateAsync({
        telegram_user: id,
        Action: "     ",
      });
      navigate(-1);
    }
  };

  const onComplete = () => {
    if (activeQuestion + 1 < data?.data?.data?.length) {
      setActiveQuestion(activeQuestion + 1)
      logsMutation.mutate({
        telegram_user: id,
        Action: `     ${data?.data?.data[activeQuestion].id}`,
      });
    } else {
      logsMutation.mutate({
        telegram_user: id,
        Action: "       ",
      });
      setTags()
      navigate('/collection')
    }
  };

  useEffect(() => {
    tg.onEvent("mainButtonClicked", onComplete);
    return () => {
      tg.offEvent("mainButtonClicked", onComplete);
    };
    // eslint-disable-next-line
  }, [onComplete]);

  useEffect(() => {
    if (showMainButton) {
      tg.MainButton.show();
      tg.MainButton.setParams({
        text: "",
        color: "rgb(146, 39, 143)"
      });
    } else {
      tg.MainButton.hide()
    }
  }, [showMainButton]);
  useEffect(() => {
    return () => {
      tg.MainButton.hide();
    }
  }, []);

  useEffect(() => {
    tg.onEvent("backButtonClicked", backHandle);
    return () => {
      tg.offEvent("backButtonClicked", backHandle);
    };
    // eslint-disable-next-line
  }, [backHandle]);

  useEffect(() => {
    tg.BackButton.show();
  }, []);

  return (
    <div className={`p-7 flex-col w-full`}>
      {data?.data?.data.sort((a, b) => +a?.attributes?.Order > b?.attributes?.Order ? 1 : -1).map((item, idx) => {
        if (item.attributes.QuestionType === 'Selection(multi)' && idx === activeQuestion) {
          return <SelectionMulti questions={questions} id={id} setQuestions={setQuestions} setShowMainButton={setShowMainButton} key={idx} item={item?.attributes} itemId={item?.id} />
        }
        if (item.attributes.QuestionType === 'Selection' && idx === activeQuestion) {
          return <Selection id={id} questions={questions} setQuestions={setQuestions} setShowMainButton={setShowMainButton} key={idx} item={item?.attributes} itemId={item?.id} />
        }
        if (item.attributes.QuestionType === 'Text' && idx === activeQuestion) {
          return <InputAnswer id={id} questions={questions} setQuestions={setQuestions} setShowMainButton={setShowMainButton} key={idx} item={item?.attributes} itemId={item?.id} />
        }
        if (item.attributes.QuestionType === 'Range' && idx === activeQuestion) {
          return <Range id={id} questions={questions} setQuestions={setQuestions} setShowMainButton={setShowMainButton} key={idx} item={item?.attributes} itemId={item?.id} />
        }
      })}
      {/* {showMainButton ? (
        <SubmitButton onClick={() => onComplete()}>
          
        </SubmitButton>
      ) : null} */}
    </div>
  );
};

export default Questions;
