import { useState } from 'react'

const Filters = ({ data, activeDevelopment, setActiveDevelopment }) => {
  const [open, setOpen] = useState(false)
  return data?.length > 1 ? <div className="sticky top-0 bg-white p-5 border-slate-200 border-b">
    <div className={`relative flex flex-wrap items-baseline gap-1 overflow-hidden pr-10 ${!open && "h-[34px]"}`}>
      {!open ? <div onClick={() => setOpen(!open)} className='transition-all duration-500 active:scale-95 active:brightness-105 absolute right-0 top-0 w-8 h-8 rounded-full bg-no-repeat bg-[url(./assets/images/arrow-close.svg)] bg-cover' /> : <div onClick={() => setOpen(!open)} className='transition-all duration-500 active:scale-95 active:brightness-105 absolute right-0 top-0 w-8 h-8 rounded-full bg-no-repeat bg-[url(./assets/images/arrow-open.svg)] bg-cover' />}
      {data?.map(item => { return item?.attributes?.name ? <div onClick={() => setActiveDevelopment(item)} className={`whitespace-nowrap transition-all duration-500 active:scale-95 active:brightness-105 flex text-xs justify-center items-center cursor-pointer px-3 py-2 rounded-full ${activeDevelopment?.find(el => el?.id === item?.id) ? "bg-itemDevelopment text-white" : "bg-slate-100"}`} key={item?.id}>{item?.attributes?.name}</div> : null })}
    </div>
  </div> : null
}

export default Filters;